.search-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-input {
    padding: 11px;
    width: 20%;
    text-align: center;
    margin-top: 10px;
    border-width: 0px;
    background: #a592f2;
    color: #e7e9f0;
    font-size: 15px;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.search-input::placeholder {
    color: #e7e9f0;
}

.search-input:focus {
    outline: none;
}

